@import "variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .header {
      .container {
        .header__mainSearch {
          .search {
            margin-top: 40px;
          }
          .numbers {
            margin-top: 25px;
            ul {
              li {
                i {
                  width: 45px;
                  height: 45px;
                }
              }
            }
          }
          .workTime {
            margin: 40px 0 0 15px;
          }
        }
        .header__cabinet {
          margin-top: 40px;
          ul {
            li {
              &:nth-child(3) {
                a{
                  i {
                    margin: 0 !important;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1310px), screen and (max-device-width: 1310px){
  body{
    font-size: 14px;
    line-height: 16px;
    .header{
      .container{
        .header__logo{
          a{
            margin-left: 0;
          }
        }
        .header__mainSearch {
          .search{
            input{
              height: 30px;
              line-height: 30px;
            }
            i{
              font-size: 1em;
            }
          }
          .numbers{
            i{
              width: 30px;
              height: 30px;
              font-size: 1.5em;
            }
          }
          .workTime{
            margin: 40px 20px 0 10px;
            ul{
              margin-left: 10px;
              li{
                span{
                  font-size: 1em;
                  line-height: 1.125em;
                }
              }
            }
          }
        }
        .header__cabinet{
          ul{
            li{
              a{
                span{
                  i{
                    padding: 0 10px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    .header{
      .container{
        grid-template-areas: "headline headline headline"
        "logo search cabinet"
        "navPanel navPanel navPanel"
        "secondNav secondNav secondNav";
        .header__logo{
          margin-top: 20px;
        }
        .header__mainSearch{
          .search{
            margin-top: 20px;
            input{
              width: 80%;
            }
          }
          .numbers{
            margin-top: 0;
          }
          .workTime{
            margin: 20px 20px 0 10px;
          }
        }
        .header__cabinet{
          margin-top: 25px;
          ul{
            li{
              a{
                span{
                  i{
                    padding: 0 5px;
                  }
                }
              }
              &:nth-child(3){
                a{
                  span{
                    margin: 5px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1000px), screen and (max-device-width: 1000px){
  body{
    font-size: 12px;
    line-height: 14px;
    .header{
      .container{
        .header__mainSearch{
          .numbers{
            ul{
              flex-direction: column;
              li{
                a{
                  font-size: 1.375em;
                  line-height: 1.5em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 780px), screen and (max-device-width: 780px){
  body{
    .header{
      .container{
        .header__cabinet{
          ul{
            flex-direction: column;
            li{
              margin: 5px 0;
              width: 100%!important;
              a{
                justify-content: unset;
                span{
                  i{
                    padding: 0 10px;
                  }
                }
              }
              &:nth-child(3){
                a{
                  margin-top: 0;
                }
              }
            }
          }
        }
        .header__nav-panel{
          margin-top: 30px;
          &__major,
          &__goods{
            ul{
              li{
                a{
                  font-size: 1em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .header{
      .container{
        .header__mainSearch{
          flex-direction: column;
          .numbers,
          .workTime{
            margin: 10px 0 0;
            justify-content: center;
            ul{
              display: flex;
              flex-direction: row;
              justify-content: center;
              align-items: center;
            }
          }
        }
        .header__nav-panel{
          align-items: center;
          .mobileDrop{
            display: block;
            width: 90%;
            font: 1.25em/2em @sansbold;
            color: @linkColor;
            text-align: center;
            background-color: @mainColor;
            border-radius: 5px;
            text-decoration: none;
          }
          &__major,
          &__goods{
            display: none;
            position: fixed;
            z-index: 20;
            ul{
              flex-direction: column;
              li{
                width: 300px;
                a{
                  font-size: 1.5em;
                }
              }
              .footer{
                display: block;
              }
            }
          }
          &__major{
            top: 190px;
            left: -160px;
          }
          &__goods{
            top: 0;
            left: -160px;
          }
        }
      }
    }
  }
}

@media screen and (max-width: 680px), screen and (max-device-width: 680px){
  body{
    .header{
      .container{
        grid-template-columns: 25% 65% 10%;
        .header__cabinet{
          ul{
            li{
              a{
                justify-content: center;
                span{
                  &:nth-child(2){
                    display: none;
                  }
                }
              }
              &:nth-child(3){
                a{
                  justify-content: center;
                  border: none;
                }
              }
            }
          }
        }
      }
    }
  }
}