@import "components/variables";
@import "components/m_header";
@import "components/m_main_drop";
@import "components/m_howToMakeOrder";
@import "components/m_address";
@import "components/m_fixed_nav";
@import "components/m_authorization";
@import "components/m_deliveryPopup";

@media screen and (max-width: 1450px), screen and (max-device-width: 1450px) {
  body {
    .content {
      .container {
        grid-template-columns: 25% 40% 35%;
        .content__productCard__descr{
          margin-top: 85px;
          .packaging,
          .plate{
            button{
              width: 105px;
            }
          }
          .consultation{
            a{
              width: 250px;
            }
          }
        }
        .content__recomend{
          .slider {
            ul {
              li{
                width: 200px;
                a{
                  img{
                    margin-top: 30px;
                    width: 175px;
                    height: 185px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1300px), screen and (max-device-width: 1300px) {
  body {
    .content {
      .container {
        .content__recomend{
          .slider {
            ul {
              li{
                width: 225px;
                a{
                  img{
                    margin-top: 15px;
                    width: 200px;
                    height: 200px;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

