@import "variables";

@media screen and (max-width: 1500px), screen and (max-device-width: 1500px){
  body{
    .content{
      .container{
        .content__howToMakeOrder {
          div {
            .info {
              margin-left: 55px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1050px), screen and (max-device-width: 1050px){
  body{
    .content{
      .container{
        .content__howToMakeOrder{
          div{
            height: 150px;
            .icons{
              .arrow{
                margin: 15px 0 0 15px;
              }
              .cart,
              .box,
              .truck{
                margin-top: 20px;
                width: 70px;
                height: 70px;
              }
            }
            .learnMore{
              a{
                width: 125px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 940px), screen and (max-device-width: 940px){
  body{
    .content{
      .container{
        .content__howToMakeOrder{
          div{
            height: 150px;
            .icons{
              .cart,
              .box,
              .truck{
                width: 50px;
                height: 50px;
              }
            }
            .learnMore{
              a{
                width: 100px;
                height: 35px;
                font-size: 1em;
                line-height: 35px;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 740px), screen and (max-device-width: 740px){
  body{
    .content{
      .container{
        .content__howToMakeOrder{
          margin: 25px 0;
          flex-direction: column;
          div{
            justify-content: space-around;
            align-items: center;
            margin: 5px 0;
            width: 100%;
            height: 90px;
            .icons{
              order: 1;
              .arrow{
                margin: 0;
              }
              .cart,
              .box,
              .truck{
                margin: 0 0 0 15px;
              }
            }
            .learnMore{
              order: 3;
              a{
                width: 165px;
              }
            }
            .info{
              order: 2;
              margin-left: 0;
            }
            .icons,
            .learnMore,
            .info{
              width: unset;
            }
          }
        }
      }
    }
  }
}